<template>
  <div class="content">
    <tab :yearAchtive="yearAchtive" @onclick="selectTime" :data='this.getRespone.tabJson'/>
    <ve-ring :data="payAway" :settings="chartSettings"></ve-ring>
    <section class="pay_table">
      <div class="table_content" v-for="(item,index) of payItem" :key="index" :style='{"background": index % 2 == 0 ? "#fff":"#F2F2F2"}'>
        <p>{{item.name}}</p>
        <p>￥{{item.money}}</p>
        <p>{{item.bfb}}</p>
        <p>{{item.num}}</p>
      </div>
    </section>
  </div>
</template>

<script>
import tab from "@/components/tab";
export default {
  data() {
    this.chartSettings = {
      roseType: "radius"
    };
    return {
      yearAchtive: 1,
      payItem: [
        { name: "支付宝", money: "3000", bfb: "40", num: "554" },
        { name: "微信", money: "4000", bfb: "40", num: "554" },
        { name: "储蓄卡", money: "5000", bfb: "40", num: "554" },
        { name: "现金", money: "5000", bfb: "40", num: "554" },
        { name: "美团", money: "1000", bfb: "40", num: "554" },
        { name: "其他", money: "800", bfb: "40", num: "554" }
      ],
      payAway:[]
    };
  },
  mounted() {
    var payItem = this.payItem;
    payItem.forEach(el => {
      el.money = this.util.setMoney(el.money);
    });
    this.getPayAway()
  },
  methods: {
    selectTime(e) {
      var year = this.util.getTime(new Date()).substring(0, 4);
      var month = this.util.getTime(new Date()).substring(5, 7);
      var day = this.util.getTime(new Date()).substring(8, 10);
      this.yearAchtive = e;
    },
    getPayAway() {
      var self = this;
      var rows = self.getRespone.payAway;
      var columns = ["name", "money", "bfb", "num"];
      self.payAway = { columns, rows };
    }
  },
  components: {
    tab
  }
};
</script>

<style lang="scss" scoped>
.pay_table {
  background-color: $fc;
  margin: 0.5rem;
  // border: 0.02rem solid $Tline;
  box-shadow: 0rem 0.3rem 0.8rem $Tline;
  .table_content {
    @include grid(4, auto);
    @include sc(0.8rem, $line);
    padding: 0.3rem;
    p {
      margin: 0.2rem 0rem;
    }
  }
}
</style>